@media screen and (max-width: 1200px) {
    .about{
        .container{
            margin-top: 54px;
            padding: 0 28px;
            display: flex;
            flex-direction: column;
            .about-image{
                width: 100%;
                height: 319px;
                margin-left: auto;
                margin-right: auto;
                margin-top: 25px;
                display: flex;
                align-items: center;
                img{
                    width: 100%;
                    max-width: 326px;
                    // position: relative;
                    // top: 0px;
                    // left: 0px;
                }
            }
            .text-part{
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 100%;
                .template-title {
                    &:after {
                        background-color: $color-secondary;
                        transform: translate(-50%, 0%);
                    }
                    margin-bottom: 65px;
                }
                p{
                    // text-align: center;
                    color: #FFF;
                    font-size: 16px;
                    font-weight: 300;
                    line-height:  149.5%;
                }
            }
        }
    }
    .questions{
        .container{
            max-width: 1175px;
            .accordion{
                max-width: 716px;
                display: block;
                margin-right: auto;
                margin-left: auto;
                margin-bottom: 28px;
                .accordion-item{
                    margin-top: 16px;
                    border: 1px solid #3A444E;
                    border-radius: 10px;
                    // overflow: hidden;    
                    max-width: 716px;
                    width: 100%;
                    max-height: 152px;
                    background-color: transparent;
                    .accordion-header{
                        max-height: 60px !important;
                    }
                    .accordion-collapse.collapse.show{
                        .accordion-body{
                            padding: 0px 15px 12px 22px;
                            background-color: #FFF;
                            box-shadow: 5px 15px 40px 0px rgba(11, 24, 97, 0.32);
                            // border-radius: 0 0 10px 10px;
                            .accordion-body-text{
                                color: #282B38;
                                font-size: 14px;
                                font-weight: 300;
                                line-height: 144.5%;
                                margin: 0;
                            }
                        }
                    }
                    .accordion-collapse.collapsing{
                        .accordion-body{                            
                            padding: 0px 15px 12px 22px;
                            background-color: #FFF;
                            box-shadow: 5px 15px 40px 0px rgba(11, 24, 97, 0.32);
                            // border-radius: 0 0 10px 10px;
                            .accordion-body-text{
                                color: #282B38;
                                font-size: 14px;
                                font-weight: 300;
                                line-height: 144.5%;
                                vertical-align: text-top;
                                max-height: 80px;
                            }
                        }
                    }
                    .accordion-button{
                        padding: 12px 0 9px 22px !important;
                        background-color: #FFF;
                        box-shadow: 5px 15px 40px 0px rgba(11, 24, 97, 0.32);
                        box-shadow: none;
                        max-height: 64px;
                        max-width: 716px;
                        width: 100%;
                        // height: 100%;
                        .accordion-btn-text{
                           color: #282B38;
                           font-size: 16px;
                           font-weight: 700;
                           line-height: 124.5%;
                           padding: 0;
                           margin-bottom: 0;
                        }
                    }
                    .accordion-button.collapsed{
                        background-color: #282B38;   
                        padding: 12px 0 9px 22px;  
                        .accordion-btn-text{
                            color: #FFF;
                            font-size: 16px;
                            font-weight: 700;
                            line-height: 124.5%;
                            padding: 0;
                            margin-bottom: 0;
                         }
                    }
                    .accordion-button::after {   
                        background-size: auto;
                        background-position: center;
                        width: 27px;
                        height: 27px;
                        transform: rotate(360deg);
                        margin-right: 19px;
                        box-shadow: none;
                        background-image: url("assets/img/arrow-circle-left 2.png");
                    }
                    .accordion-button:not(.collapsed)::after {
                        background-size: auto;
                        background-position: center;
                        transform: rotate(270deg);
                        margin-right: 19px;
                        box-shadow: none;
                        background-image:url("assets/img/arrow-circle-left 1.png");
                    }
                }       
            }
            .cont-form{
                // max-width: 716px;
                max-width: 390px;
                width: 100%;
                display: block;
                justify-self: start;
                height: 553px;
                vertical-align: top;
                display: block;
                margin-right: auto;
                margin-left: auto;
                .question-form{
                    background-color: #363A4D;
                    border-radius: 20px;
                    padding: 38px 40px;
                    height: 100%;
                    margin-left: auto;
                    margin-right: auto;
                    filter: drop-shadow(5px 15px 40px rgba(11, 24, 97, 0.32));
                    .row{
                        // max-width: 636px;
                        max-width: 310px;
                        margin-left: auto;
                        margin-right: auto;
                       .input-box{
                           width: 100%;
                        //    max-width: 636px;
                           max-width: 310px;
                           height: 56px;
                           border-radius: 10px;
                           background: #FFF;
                           margin-bottom: 14px;
                           display: flex;
                           flex-direction: column;
                           align-items: center;
                           position: relative;
                           border: none;
                               &::placeholder {
                                   color: #CBCBCB;
                               }
                               &:focus-visible {
                                   outline: none;
                               }             
                               &:hover {
                                   box-shadow: 0px 0px 4px 2px $color-primary;
                               }
                    
                               &:focus {
                                   box-shadow: 0px 0px 4px 2px $color-secondary;
                               }
                       }
                       .question-form-text{
                           padding: 0;
                           align-items: start;
                           .question-form-title{
                               color: #FFF;
                               font-size: 24px;
                               font-weight: 700;
                               line-height: 126%;
                               margin-bottom: 8px;
                           }
                           .question-form-subtitle{
                               color: #FFF;
                               font-size: 17px;
                               font-weight: 300;
                               line-height: 126%;
                               margin-bottom: 23px;
                           }
                       }
                    }
                }
            }
        }
    }
    footer{
        height: 100px;
        margin-top: auto;
        // max-width: 1140px;
        // width: 100%;
        // margin-left: auto;
        // margin-right: auto;
        .container{
            .template-title{
                margin-bottom: 40px;
                max-width: 1140px;
                &:after{
                    background-color: $color-primary;
                    max-width: 1140px;
                    width: 100%;
                }
            }
            .content-part{
                display: flex;
                flex-direction: row;
                .info-column{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    max-width: 263px;
                    width: 100%;
                    margin-left: auto;
                    margin-right: auto;
                    .navbar-brand{
                        .brand-img{
                            margin-bottom: 28.65px;
                        }
                    }
                    .footer-adress{
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        p{
                            margin-bottom: 8px;
                            color: #FFF;
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 162.5%;
                        }
                    }
                    .nav-icons{
                        display: flex;
                        flex-direction: row;
                         .nav-icon{
                              width: 33px;
                              height: 33px;
                              border-radius: 50%;
                              overflow: hidden;
                         }
                    }
                }
                .links-columns{
                    display: none;
                }
            }
        }
    }
    
     
}
@media screen and (max-width: 1275px) {
    .ready-to-work{
        .container{
            .template-title{
                margin-bottom: 43px;
                h2{
                    font-size: 24px;
                }
                h3{
                    color: #c8c8c8;
                    font-size: 16px;
                    text-transform: none;
                }
                &:after{
                    background-color: $color-secondary;
                    // display: none;
                }
            }
    
            .ready-blocks-row{
                .ready-block{
                    margin-top: 43px;
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    .ready-text-part{
                        margin-left: auto;
                        margin-right: auto;
                        width: 100%;
                        max-width: 528px;
                        align-items: center;
                        box-shadow: none;
                        p{
                            text-align: justify;
                            color: #FFF;
                            font-size: 16px;
                            font-weight: 300;
                            line-height: 156.5%;
                            margin-bottom: 0;
                        }
                        .first-p-in-second{
                            margin-top: 0;
                        }
                        .ready-button{
                            margin-left: auto;
                            margin-right: auto;
                            width: 100%;
                            .icon{
                                margin-left: auto;
                            }
                        }
                    }
                    .ready-image-part{
                         margin-bottom: 43px;
                         height: auto;
                         width: auto;
                         margin-left: auto;
                         margin-right: auto;
                        img{
                            // width: calc(100% + 40px);
                            // margin-left: -20px;
                            // margin-right: -20px;
                            // height: calc(100% + 40px);
                            // margin-bottom: -27px;
                            // margin-top: -27px;
    
                            // width: calc(100% + 80px);
                            // margin-left: -34.5px;
                            // margin-right: -27.5px;
                            // height: calc(100% + 83px);
                            // margin-bottom: -27px;
                            // margin-top: -27px;
                            filter: drop-shadow(5px 15px 40px rgba(11, 24, 97, 0.32));
                            height: auto;
                            width:100%;
                            // box-shadow: 5px 15px 40px 0px rgba(11, 24, 97, 0.32);
                        }
                        // }
                    }
                    &.first-ready{
                        flex-direction: column-reverse;
                        margin-top: 0;
                        .ready-text-part{
                            margin-right: auto;
                            margin-left: auto;
                        }                   
                        .ready-image-part{
                            margin-left: auto;
                            margin-right: auto;
                            // margin-bottom: 5px;
                        } 
                    }
                }
            }
        }
    }
}